@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  background-color: #68b738;
  border: none;
  padding: 10px 25px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .button--secondary {
    margin: 0 35px;
    align-self: stretch;
  }
}

@media (min-width: 1280px) {
  .main-header {
    position: relative;
  }
}

@media (min-width: 768px) {
  .main-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
}
@media (min-width: 1280px) {
  .main-header__wrapper {
    padding-top: 53px;
  }
  .main-header__wrapper::before {
    content: "";
    position: absolute;
    width: 50%;
    height: 694px;
    top: 0;
    left: 50%;
    background-image: url("../img/offer-cat-desktop.jpg");
    background-repeat: no-repeat;
    background-position: right;
    z-index: -1;
    background-color: #64a73c;
  }
}
@media (min-width: 1280px) and (min-resolution: 2dppx) {
  .main-header__wrapper::before {
    background-image: url("../img/offer-cat-desktop@2x.jpg");
    background-size: 112% 694px;
  }
}

@media (min-width: 1280px) {
  .main-header__wrapper--background-off::before {
    content: none;
  }
}

.main-header__logo {
  color: #111111;
  text-decoration: none;
  display: flex;
  align-items: center;
  height: 65px;
}

@media (min-width: 768px) {
  .main-header__logo img {
    width: 174px;
    height: 52px;
  }
  .main-header__logo img:hover {
    opacity: 0.8;
  }
  .main-header__logo img:active {
    opacity: 0.6;
  }
}
@media (min-width: 1280px) {
  .main-header__logo img {
    width: 202px;
    height: 59px;
  }
}

.main-header__logo picture {
  position: absolute;
}

.main-header__logo span {
  height: 18px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .main-header__logo span {
    display: none;
  }
}

.page__main--overflow {
  overflow-x: hidden;
}

.main-navigation {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
}
@media (min-width: 768px) {
  .main-navigation {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1280px) {
  .main-navigation {
    font-size: 20px;
    line-height: 30px;
  }
}

.main-navigation__toggle {
  position: absolute;
  width: 30px;
  height: 38px;
  padding: 0;
  right: 0;
  top: -57px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 1.5px;
  right: 0;
  top: 14px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
  right: 0;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}

.main-navigation--closed .main-navigation__list {
  display: none;
}

@media (min-width: 768px) {
  .main-navigation .main-navigation__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    width: 440px;
  }
}
@media (min-width: 1280px) {
  .main-navigation .main-navigation__list {
    width: 478px;
  }
}

.site-list {
  text-transform: uppercase;
  text-align: center;
  margin: 0 -20px;
}
@media (min-width: 768px) {
  .site-list {
    margin: 0;
  }
}

.site-list__item {
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
}
.site-list__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .site-list__item {
    padding: 0;
    border-top: none;
  }
  .site-list__item:last-child {
    border-bottom: none;
  }
}

@media (min-width: 768px) {
  .site-list__item--current {
    position: relative;
  }
  .site-list__item--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 768px) and (min-width: 1280px) {
