  }
}

.site-list__link--color.site-list__item--current::after {
  background-color: #68b738;
}

.site-list__link {
  text-decoration: none;
  color: #000000;
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:active {
  opacity: 0.3;
}
@media (min-width: 1280px) {
