.slider__field {
  color: #666666;
  position: absolute;
  top: 108px;
  left: 120px;
  width: 40px;
  height: 40px;
  z-index: 2;
}
.slider__field:hover svg {
  fill: #68b738;
}
.slider__field:hover svg {
  stroke: #68b738;
}
.slider__field:hover::before {
  background-color: #68b738;
