.main-footer {
  background-color: $accent-secondary;
  padding: 40px 0;

  @include above-tablet() {
    padding: 0;
  }
}

.main-footer__wrapper {
  position: relative;

  @include above-tablet() {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 56px;
    padding-top: 56px;
  }

  @include above-desktop() {
    padding-bottom: 67px;
    padding-top: 67px ;
  }
}

.main-footer__logo::after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: $color-darken-gray-var2;
  left: 20px;
  right: 20px;
  top: 44px;
  box-shadow: 0 62px 0 0 $color-darken-gray-var2;

  @include above-tablet() {
    display: none;
  }
}

.main-footer__logo svg {
  color: $text-contact;
  display: block;
  margin: 0 auto;
  margin-bottom: 40px;

  &:hover,
  &:focus {
    color: $logo-hover;
  }

  &:active {
    color: $logo-active;
  }

  @include above-tablet() {
    margin-bottom: 0;
    width: 143px;
    height: 26px;
  }
}

.main-footer__social {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;

  @include above-tablet() {
    margin-bottom: 0;
  }
}

